import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import usePerformanceMode from '../hooks/usePerformanceMode';

const ParticleBackground = () => {
  const [particles, setParticles] = useState([]);
  const [connections, setConnections] = useState([]);
  const isLowPerformance = usePerformanceMode();

  useEffect(() => {
    const generateParticles = () => {
      const newParticles = [];
      const particleTypes = ['circle', 'square', 'triangle', 'star'];
      const colors = ['neon-blue', 'neon-violet', 'neon-magenta', 'neon-cyan'];
      
      const particleCount = isLowPerformance ? 10 : 30;
      for (let i = 0; i < particleCount; i++) {
        newParticles.push({
          id: i,
          x: Math.random() * 100,
          y: Math.random() * 100,
          size: Math.random() * 6 + 2,
          delay: Math.random() * 8,
          duration: Math.random() * 6 + 4,
          type: particleTypes[Math.floor(Math.random() * particleTypes.length)],
          color: colors[Math.floor(Math.random() * colors.length)],
          speed: Math.random() * 2 + 1,
          direction: Math.random() * 360,
        });
      }
      setParticles(newParticles);
    };

    const generateConnections = () => {
      const newConnections = [];
      const connectionCount = isLowPerformance ? 3 : 8;
      for (let i = 0; i < connectionCount; i++) {
        newConnections.push({
          id: i,
          startX: Math.random() * 100,
          startY: Math.random() * 100,
          endX: Math.random() * 100,
          endY: Math.random() * 100,
          delay: Math.random() * 4,
          duration: Math.random() * 3 + 2,
        });
      }
      setConnections(newConnections);
    };

    generateParticles();
    generateConnections();
  }, [isLowPerformance]);

  const getParticleShape = (type, size, color) => {
    const baseClasses = `absolute opacity-60 bg-${color}`;
    
    switch (type) {
      case 'circle':
        return (
          <motion.div
            className={`${baseClasses} rounded-full`}
            style={{ width: `${size}px`, height: `${size}px` }}
            animate={{
              scale: [1, 1.5, 1],
              rotate: [0, 180, 360],
            }}
            transition={{
              duration: 4,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        );
      case 'square':
        return (
          <motion.div
            className={`${baseClasses} rounded-sm`}
            style={{ width: `${size}px`, height: `${size}px` }}
            animate={{
              rotate: [0, 90, 180, 270, 360],
              scale: [1, 0.8, 1],
            }}
            transition={{
              duration: 6,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        );
      case 'triangle':
        return (
          <motion.div
            className={`${baseClasses}`}
            style={{ 
              width: `${size}px`, 
              height: `${size}px`,
              clipPath: 'polygon(50% 0%, 0% 100%, 100% 100%)'
            }}
            animate={{
              rotate: [0, 120, 240, 360],
              scale: [1, 1.2, 1],
            }}
            transition={{
              duration: 5,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        );
      case 'star':
        return (
          <motion.div
            className={`${baseClasses}`}
            style={{ 
              width: `${size}px`, 
              height: `${size}px`,
              clipPath: 'polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%)'
            }}
            animate={{
              rotate: [0, 180, 360],
              scale: [1, 1.3, 1],
            }}
            transition={{
              duration: 3,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        );
      default:
        return (
          <motion.div
            className={`${baseClasses} rounded-full`}
            style={{ width: `${size}px`, height: `${size}px` }}
          />
        );
    }
  };

  return (
    <div className="fixed inset-0 pointer-events-none z-0 overflow-hidden">
      {/* Main particles */}
      {particles.map((particle) => (
        <motion.div
          key={particle.id}
          className="absolute will-change-transform"
          style={{
            left: `${particle.x}%`,
            top: `${particle.y}%`,
            transform: 'translateZ(0)', // Force hardware acceleration
          }}
          animate={{
            y: [-20, 20, -20],
            x: [-10, 10, -10],
            opacity: [0.2, 0.8, 0.2],
          }}
          transition={{
            duration: particle.duration,
            delay: particle.delay,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        >
          {getParticleShape(particle.type, particle.size, particle.color)}
        </motion.div>
      ))}

      {/* Connection lines */}
      {connections.map((connection) => (
        <motion.div
          key={connection.id}
          className="absolute"
          style={{
            left: `${Math.min(connection.startX, connection.endX)}%`,
            top: `${Math.min(connection.startY, connection.endY)}%`,
            width: `${Math.abs(connection.endX - connection.startX)}%`,
            height: `${Math.abs(connection.endY - connection.startY)}%`,
          }}
          animate={{
            opacity: [0, 0.3, 0],
            scale: [0.8, 1.2, 0.8],
          }}
          transition={{
            duration: connection.duration,
            delay: connection.delay,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        >
          <motion.div
            className="w-full h-px bg-gradient-to-r from-neon-blue/30 to-neon-violet/30"
            style={{
              transformOrigin: 'left center',
              transform: `rotate(${Math.atan2(
                connection.endY - connection.startY,
                connection.endX - connection.startX
              ) * 180 / Math.PI}deg)`,
            }}
          />
        </motion.div>
      ))}

      {/* Floating orbs */}
      <motion.div
        animate={{
          x: [0, 100, 0],
          y: [0, -50, 0],
          scale: [1, 1.5, 1],
        }}
        transition={{
          duration: 20,
          repeat: Infinity,
          ease: "easeInOut",
        }}
        className="absolute top-1/4 left-1/4 w-32 h-32 bg-gradient-to-r from-neon-blue/20 to-neon-violet/20 rounded-full blur-xl"
      />
      <motion.div
        animate={{
          x: [0, -80, 0],
          y: [0, 60, 0],
          scale: [1, 0.8, 1],
        }}
        transition={{
          duration: 25,
          repeat: Infinity,
          ease: "easeInOut",
        }}
        className="absolute bottom-1/4 right-1/4 w-24 h-24 bg-gradient-to-r from-neon-magenta/20 to-neon-cyan/20 rounded-full blur-xl"
      />

      {/* Grid pattern */}
      <div className="absolute inset-0 opacity-10">
        <div className="w-full h-full" style={{
          backgroundImage: `
            linear-gradient(rgba(0, 245, 255, 0.1) 1px, transparent 1px),
            linear-gradient(90deg, rgba(0, 245, 255, 0.1) 1px, transparent 1px)
          `,
          backgroundSize: '50px 50px'
        }} />
      </div>
    </div>
  );
};

export default ParticleBackground;
