import React, { useState, useEffect } from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { useLanguage } from '../contexts/LanguageContext';

const Hero = () => {
  const { t, toggleLanguage } = useLanguage();
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const { scrollY } = useScroll();
  const y = useTransform(scrollY, [0, 300], [0, -50]);
  const opacity = useTransform(scrollY, [0, 300], [1, 0]);

  useEffect(() => {
    const handleMouseMove = (e) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Enhanced animated background */}
      <div className="absolute inset-0 bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900">
        <div className="absolute inset-0 bg-gradient-to-tr from-neon-blue/1 via-transparent to-neon-violet/1" />
        <div className="absolute inset-0 bg-gradient-to-bl from-transparent via-neon-magenta/3 to-transparent" />
      </div>

      {/* Dynamic background elements */}
      <div className="absolute inset-0">
        <motion.div
          animate={{
            x: [0, 100, 0],
            y: [0, -50, 0],
            rotate: [0, 360, 0],
            scale: [1, 1.2, 1]
          }}
          transition={{ duration: 20, repeat: Infinity, ease: "easeInOut" }}
          className="absolute top-20 left-20 w-32 h-32 border-2 border-neon-blue/30 rounded-full"
        />
        <motion.div
          animate={{
            x: [0, -80, 0],
            y: [0, 60, 0],
            rotate: [360, 0, 360],
            scale: [1, 0.8, 1]
          }}
          transition={{ duration: 25, repeat: Infinity, ease: "easeInOut" }}
          className="absolute bottom-20 right-20 w-24 h-24 bg-neon-violet/20 rounded-full"
        />
      </div>

      {/* Mouse-following glow effect - Optimized */}
      <motion.div
        className="absolute w-96 h-96 bg-neon-blue/10 rounded-full blur-3xl pointer-events-none will-change-transform"
        style={{ transform: 'translateZ(0)' }}
        animate={{
          x: mousePosition.x - 192,
          y: mousePosition.y - 192,
        }}
        transition={{ type: "spring", stiffness: 30, damping: 20 }}
      />

      {/* Language Toggle Button */}
      <motion.button
        initial={{ opacity: 0, x: 50 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ delay: 1.5, duration: 0.8 }}
        onClick={toggleLanguage}
        className="fixed top-6 right-6 z-50 px-4 py-2 bg-dark-surface/80 backdrop-blur-sm border border-neon-cyan rounded-full text-neon-cyan font-semibold hover:bg-neon-cyan hover:text-dark-bg transition-all duration-300 neon-glow-cyan"
      >
{t('hero.language')}
      </motion.button>

      {/* Main content */}
      <motion.div 
        style={{ y, opacity }}
        className="relative z-10 text-center px-4 max-w-6xl mx-auto"
      >
        {/* Enhanced Logo/Title */}
        <motion.div
          initial={{ opacity: 0, y: 50, scale: 0.8 }}
          animate={{ opacity: 1, y: 0, scale: 1 }}
          transition={{ duration: 1.2, ease: "easeOut" }}
          className="mb-8"
        >
          <motion.h1 
            className="text-6xl md:text-8xl lg:text-9xl font-black gradient-text mb-4 will-change-transform"
            style={{
              background: "linear-gradient(45deg, #00f5ff, #8b5cf6, #f472b6, #06b6d4, #00f5ff)",
              backgroundSize: "300% 300%",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
              backgroundClip: "text",
              transform: 'translateZ(0)'
            }}
          >
            {t('hero.title')}
          </motion.h1>
          <motion.div
            initial={{ scale: 0, rotate: -180 }}
            animate={{ scale: 1, rotate: 0 }}
            transition={{ delay: 0.5, duration: 1, type: "spring", bounce: 0.6 }}
            className="text-2xl md:text-4xl font-light text-neon-cyan neon-glow-cyan"
          >
            {t('hero.subtitle')}
          </motion.div>
        </motion.div>

        {/* Enhanced Slogan */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8, duration: 0.8 }}
          className="mb-12"
        >
          <motion.p
            className="text-xl md:text-3xl font-light text-gray-300"
            animate={{
              textShadow: [
                "0 0 10px rgba(0, 245, 255, 0.5)",
                "0 0 20px rgba(139, 92, 246, 0.5)",
                "0 0 10px rgba(0, 245, 255, 0.5)"
              ]
            }}
            transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
          >
            {t('hero.slogan')}
          </motion.p>
        </motion.div>

        {/* Enhanced CTA Buttons */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1.2, duration: 0.8 }}
          className="flex flex-col sm:flex-row gap-4 justify-center items-center"
        >
          <motion.button
            whileHover={{ 
              scale: 1.05,
              boxShadow: "0 0 30px rgba(0, 245, 255, 0.6)",
              y: -2
            }}
            whileTap={{ scale: 0.95 }}
            className="neon-button touch-target relative px-8 py-4 bg-transparent border-2 border-neon-blue text-neon-blue font-semibold text-lg rounded-full hover:bg-neon-blue hover:text-dark-bg transition-all duration-300 neon-glow-blue group"
          >
            <span className="relative z-10">{t('hero.exploreApps')}</span>
            <motion.div
              className="absolute inset-0 bg-gradient-to-r from-neon-blue to-neon-cyan rounded-full opacity-0 group-hover:opacity-20"
              animate={{ rotate: [0, 360] }}
              transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
            />
          </motion.button>
          
          <motion.button
            whileHover={{ 
              scale: 1.05,
              boxShadow: "0 0 30px rgba(139, 92, 246, 0.6)",
              y: -2
            }}
            whileTap={{ scale: 0.95 }}
            className="neon-button touch-target relative px-8 py-4 bg-transparent border-2 border-neon-violet text-neon-violet font-semibold text-lg rounded-full hover:bg-neon-violet hover:text-dark-bg transition-all duration-300 neon-glow-violet"
          >
{t('hero.learnMore')}
          </motion.button>
        </motion.div>

        {/* Optimized floating elements */}
        <motion.div
          animate={{ 
            y: [-10, 10, -10],
            opacity: [0.3, 0.8, 0.3]
          }}
          transition={{ duration: 4, repeat: Infinity, ease: "easeInOut" }}
          className="absolute top-20 left-10 w-4 h-4 bg-neon-violet rounded-full neon-glow-violet opacity-60 will-change-transform"
          style={{ transform: 'translateZ(0)' }}
        />
        <motion.div
          animate={{ 
            y: [10, -10, 10],
            opacity: [0.4, 0.9, 0.4]
          }}
          transition={{ duration: 3, repeat: Infinity, ease: "easeInOut", delay: 1 }}
          className="absolute top-40 right-20 w-3 h-3 bg-neon-magenta rounded-full neon-glow-magenta opacity-60 will-change-transform"
          style={{ transform: 'translateZ(0)' }}
        />
        <motion.div
          animate={{ 
            y: [-15, 15, -15],
            opacity: [0.2, 0.7, 0.2]
          }}
          transition={{ duration: 5, repeat: Infinity, ease: "easeInOut", delay: 2 }}
          className="absolute bottom-40 left-20 w-5 h-5 bg-neon-cyan rounded-full neon-glow-cyan opacity-60 will-change-transform"
          style={{ transform: 'translateZ(0)' }}
        />
      </motion.div>

      {/* Enhanced scroll indicator */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 2, duration: 1 }}
        className="absolute bottom-8 left-1/2 transform -translate-x-1/2 cursor-pointer"
        onClick={() => {
          const aboutSection = document.querySelector('section:nth-of-type(2)');
          if (aboutSection) {
            aboutSection.scrollIntoView({ behavior: 'smooth' });
          }
        }}
      >
        <motion.div
          animate={{ y: [0, 10, 0] }}
          transition={{ duration: 2, repeat: Infinity }}
          className="w-6 h-10 border-2 border-neon-blue rounded-full flex justify-center neon-glow-blue hover:border-neon-cyan transition-colors"
        >
          <motion.div
            animate={{ y: [0, 12, 0] }}
            transition={{ duration: 2, repeat: Infinity }}
            className="w-1 h-3 bg-neon-blue rounded-full mt-2"
          />
        </motion.div>
        <motion.p
          animate={{ opacity: [0.5, 1, 0.5] }}
          transition={{ duration: 2, repeat: Infinity }}
          className="text-xs text-neon-blue mt-2 text-center hover:text-neon-cyan transition-colors"
        >
          {t('hero.scrollDown')}
        </motion.p>
      </motion.div>
    </section>
  );
};

export default Hero;
