import React, { Suspense, lazy, useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { LanguageProvider } from './contexts/LanguageContext';
import Hero from './components/Hero';
import ParticleBackground from './components/ParticleBackground';

// Lazy load components for better performance
const About = lazy(() => import('./components/About'));
const Apps = lazy(() => import('./components/Apps'));
const Team = lazy(() => import('./components/Team'));
const Contact = lazy(() => import('./components/Contact'));
const AboutUs = lazy(() => import('./pages/AboutUs'));
const PrivacyPolicy = lazy(() => import('./pages/PrivacyPolicy'));
const DeleteAccount = lazy(() => import('./pages/DeleteAccount'));
const SnuvyDeleteAccount = lazy(() => import('./pages/SnuvyDeleteAccount'));
const DOSMDeleteAccount = lazy(() => import('./pages/DOSMDeleteAccount'));

function App() {
  const [currentPage, setCurrentPage] = useState('home');

  useEffect(() => {
    const path = window.location.pathname;
    if (path === '/about-us') {
      setCurrentPage('about-us');
    } else if (path === '/privacy-policy') {
      setCurrentPage('privacy-policy');
    } else if (path === '/delete-account') {
      setCurrentPage('delete-account');
    } else if (path === '/snuvy-delete-account') {
      setCurrentPage('snuvy-delete-account');
    } else if (path === '/dosm-delete-account') {
      setCurrentPage('dosm-delete-account');
    } else {
      setCurrentPage('home');
    }
  }, []);

  const renderPage = () => {
    switch (currentPage) {
      case 'about-us':
        return <AboutUs />;
      case 'privacy-policy':
        return <PrivacyPolicy />;
      case 'delete-account':
        return <DeleteAccount />;
      case 'snuvy-delete-account':
        return <SnuvyDeleteAccount />;
      case 'dosm-delete-account':
        return <DOSMDeleteAccount />;
      default:
        return (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.8 }}
          >
            <ParticleBackground />
            <Hero />
            <Suspense fallback={<div className="h-screen bg-dark-bg" />}>
              <About />
            </Suspense>
            <Suspense fallback={<div className="h-screen bg-dark-bg" />}>
              <Apps />
            </Suspense>
            <Suspense fallback={<div className="h-screen bg-dark-bg" />}>
              <Team />
            </Suspense>
            <Suspense fallback={<div className="h-screen bg-dark-bg" />}>
              <Contact />
            </Suspense>
          </motion.div>
        );
    }
  };

  return (
    <LanguageProvider>
      <div className="min-h-screen bg-dark-bg text-white overflow-x-hidden">
        {renderPage()}
      </div>
    </LanguageProvider>
  );
}

export default App;
